import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { MediaBox } from 'react-materialize';

class Reviews extends Component {
    render() {
        return (
            <div className="reviews">
                <div className="row">
                    <div className="col s12 m2 l2"> 
                    <MediaBox caption="reviewImgs" className="center-align movedown" height="250px" width="100%" src="images/gallery/20.jpg"/>
                    </div>
                    <div className="col s12 m8 l8">     
                        <blockquote>
                        "A cute, comfy set of intimate treehouses on the bank of a river where you can watch the fireflies dance while you lounge in your hammock after a fantastic meal. Do not miss the owner's scrumptious omelet breakfasts, and delicious, hearty dinners. There isn't a restaurant in nearby San Ignacio that can compare. This place was a find, and easy on the American wallet. Close to the outdoor activities of the area it's a great homebase from which to explore the Eastern interior of Belize." -JenniRG <a target="_blank" rel="noopener noreferrer" href="http://www.tripadvisor.com">Tripadvisor.com</a>
                        </blockquote>
                        <blockquote>
                        "What a fun place!! inexpensive...The owner Theo is wonderful. She cooks the most amazing food ( and she is very good with people with food issues...as I have celiac disease..and she was most accommodating ! ) Theo knows where to go..and knows who to use when you want to go on a tour. She is very helpful! great time !" -Elizabeth S Aboda <a target="_blank"  rel="noopener noreferrer" href="http://www.unusualhotelsoftheworld.com">Unusual hotels of the world.com</a>
                        </blockquote>
                        <blockquote>
                        "Parrot Nest Lodge is a secluded jungle experience but only 3 miles away from San Ignacio. The sounds of nature will greet you first thing in the morning and last thing at night... The owners, Theo and Marcus, are wonderful hosts and Parrot Nest will feel like home to you very quickly... The Mopan river is right in the backyard, so you can enjoy a refreshing swim, or go river tubing with Marcus. This was a great experience - a way to chill while listening to the river and the birds." - Debbie <a  rel="noopener noreferrer" href="http://www.debbiescaribbeanresortreviews.com" target="_blank">Debbies caribbean resort reviews.com</a>
                        </blockquote>
                    </div>
                    <div className="col s12 m2 l2">
                    <MediaBox caption="reviewImgs" className="center-align movedown" height="250px" width="100%" src="images/gallery/11.jpg"/>
                    </div>
                     </div>
                     <div className="center-align">
                        <Link to="/tours">For a list of tours, including our free tubing adventure, just click here</Link>
                    </div>
                    <div className="divider"></div>
                    <div className="row">
                        <div className="col s12 m4 l4"> 
                        <MediaBox caption="reviewImgs" className="center-align movedown" height="250px" width="100%" src="images/gallery/19.jpg"/>
                        </div>
                        <div className="col s12 m8 l8">     
                            <blockquote>
                            "Words can not describe the way I feel about this place. Theo, Marcus, Kaite, and the girls, you are all amazing people. I have never met such friendly and helpful people in my life. This is definately a home away from home. If you are looking for a 5 star hotel, then you are in the wrong place. If you are looking for something quite and peaceful, with a friendly atmosphere then you are in the right place. Theo helped us book our tours. Im happy that she did because we got to meet one of the best tour guides around, Max. If you can tour with him, i would reccomend it. If there is anything at all that you need Theo and her family are more than willing to help you . Katie (Theo`s mom) was there, she was very helpful aswell, lots of knowledge. make sure you pick up one of her books while you are there. I am really enjoying them. I can not say enough about this place. Thank you again for everything and making us feel at home. - Stephanie Herman <a target="_blank" rel="noopener noreferrer"href="http://www.tripadvisor.com">Tripadvisor.com</a>
                            </blockquote>
                            <blockquote>
                            "The Parrot Nest Lodge consists of several huts with thatched roofs that sit on 12 foot stilts amongst huge guanacaste trees in a 5 acre tropical plant just outside of San Ignacio in Belize. The lodge has one en-suite room and the rest share the ground floor open-air bathrooms that are very clean, but often house surprise insect visitors that you might not want to see in the shower. In fact the lodge, which is on the banks of the Mopan River, is surrounded by wildlife, especially parrots (hence the name) and over a hundred other types of birds. If bird-watching is not for you, you can also expect to see Coatis, agoutis, otters and giant iguanas and all number of aquatic life as you take a cool dip in the clear waters of the river." -Alessia Horwich World <a rel="noopener noreferrer" href="http://www.worldreviewer.com" target="_blank">World reviewer.com</a>
                            </blockquote>
                            <blockquote>
                            Number 1 on <a  rel="noopener noreferrer" href="http://www.travelmuse.com/articles/off-beat/top-tree-house-hotels" target="_blank">TravelMuse.com's</a> Treetop Dreams Page: "At Parrot Nest Lodge in Belize, you'll spend the night in a thatched tree house under the sprawling canopy of a native guanacaste tree. The Mopan River surrounds the lodge, and it's perfect for bird watching, both parrots and other avian varieties as well. Children under 16 stay free. Rates from $50 per night for double occupancy, $10 for each additional adult."
                            </blockquote>
                        </div> 
                    </div>
                    <div className="center-align">
                         <a target="_blank" rel="noopener noreferrer"href="http://www.bestofcayo.com">For the local Best Of Cayo website just click here</a>
                    </div>
                    <div className="divider"></div>
                    <div className="row">
                        <div className="col s12 m8 l8">     
                            <blockquote>
                            "I love, love, LOVE the Parrot's Nest. Theo, her mom and girls are AWESOME and it's worth staying there just to have the pleasure of talking with them. It's a beautiful setting with gorgeous gardens and the mopan river just down a small hill. My favorite part of my stay would have to be dinner, the food is awesome, but not as awesome as the wonderful company and conversation. If you want to feel like you're "home" in Belize, stay at the Parrot's Nest." -Megan <a target="_blank" rel="noopener noreferrer"href="http://www.unusualhotelsoftheworld.com"> Unusual hotels of the world.com</a>
                            </blockquote> 
                        </div> 
                        <div className="col s12 m4 l4"> 
                        <MediaBox caption="reviewImgs" className="center-align" height="250px" width="100%" src="images/gallery/18.jpg"/>
                        </div>
                    </div>
                    <div className="center-align">
                        <Link to="/gallery">For more pictures of the PARROT NEST LODGE just click here</Link>
                    </div>
                    <div className="divider"></div>
                        <div className="row">
                            <div className="col s12 m4 l4"> 
                            <MediaBox caption="reviewImgs" className="center-align" height="250px" width="100%" src="images/gallery/13.jpg"/>
                            </div>
                            <div className="col s12 m8 l8">     
                                <blockquote>
                                "We stayed at the parrots nest lodge for five nights and we couldn't ask for a better experience. Theo and her family are wonderful! We stayed in one of the tree houses. Every morning we awoke to sounds of birds singing outside our window. The area surrounding the lodge is gorgeous! We saw more wildlife while there then we did while taking tours through the jungle. Each evening we sat down with the rest of the guest for an unforgettable meal( Theo is one hell of a cook)and we shared stories of our 'adventures' that day. -onlyinalaska_10 <a target="_blank" rel="noopener noreferrer"href="http://www.tripadvisor.com">Tripadvisor.com</a>
                                </blockquote> 
                            </div> 
                        </div>
                        <div className="center-align">
                            <Link to="/contacts" >For a map of Bullet Tree, just click here</Link>
                        </div>
                    <div className="divider"></div>
                        <div className="row">
                            <div className="col s12 m8 l8">     
                                <blockquote>
                                
        "I Just spent the last 7 days in Belize using Parrot Nest as our home. Not only were the accommodations fantastic, the people were some of the best I have encountered. A beautiful view of the Mopan river in Belize. Only a short cab ride in to San Ignacio. A jewel in the Cayo district of Belize. Canoeing the caves and exploring the Mayan ruins of Tikal and Caracol.
        I'd give this a ranking with any jungle lodge of the Amazon!!!! Way to go!!!!!" -Bill Zimkouski <a target="_blank" rel="noopener noreferrer"href="http://www.unusualhotelsoftheworld.com">Unusual hotels of the world.com</a>
                                </blockquote> 
                            </div> 
                            <div className="col s12 m4 l4"> 
                            <MediaBox caption="reviewImgs" className="center-align" height="250px" width="100%" src="images/gallery/17.jpg"/>
                            </div>
                        </div>
            </div>
        );
    }
}

export default Reviews;


